import Friend from "./solo_friend";

function FriendsList({friends}) {
  return (
    <ul>
      {friends.map(friend => (
        <Friend
          key={friend.id}
          isOnline={friend.isOnline}
          avatar={friend.avatar}
          name={friend.name}
        />
      ))}
    </ul>
  )

}


export default FriendsList;